import React, {ButtonHTMLAttributes, DetailedHTMLProps} from 'react';
import s from './SuperButton.module.css';

// —Ç–∏–ø –ø—Ä–æ–ø—Å–æ–≤ –æ–±—ã—á–Ω–æ–π –∫–Ω–æ–ø–∫–∏, children –≤ –∫–æ—Ç–æ—Ä–æ–º —Ö—Ä–∞–Ω–∏—Ç—å—Å—è –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–Ω–æ–ø–∫–∏ —Ç–∞–º —É–∂–µ –æ–ø–∏—Å–∞–Ω
type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>;

type VariantType = 'primary' | 'secondary' | 'success' | 'danger' | 'warning';
type SizeType = 'small' | 'medium' | 'large';

type SuperButtonPropsType = DefaultButtonPropsType & {
   red?: boolean
   size?: SizeType
   variant?: VariantType
}

const SuperButton: React.FC<SuperButtonPropsType> = (
   {
      variant,
      size,
      red,
      className,
      ...restProps// –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã –ø–æ–ø–∞–¥—É—Ç –≤ –æ–±—ä–µ–∫—Ç restProps, —Ç–∞–º –∂–µ –±—É–¥–µ—Ç children
   }
) => {
   const finalClassName = `
      ${s.button} 
      ${variant ? s[variant] : ''}
      ${size ? s[size] : ''} 
      ${red ? s.red : ''}
   `;

   return (<>
         <button className={finalClassName} {...restProps} />
      </>
   );
}

export default SuperButton;
